
html,
body,
canvas {
  height: 100vh;
  width: 100vw;
  font-family: Roboto, sans-serif;
  overflow: hidden;
}

body {
  background-color: black;
  margin: 0;
}

.intro {
  color: white;
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.027);
  padding: 1rem;
}

.controlstitle {
  font-size: 1.2rem;
  color: red;
}

span {
  color: yellow;
}
.room {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.ipl-progress-indicator.available{
  opacity:0
}

.ipl-progress-indicator{
  background-color:black;
  width:100%;
  height:100%;
  position:fixed;
  opacity:1;
  pointer-events:none;
  -webkit-transition:opacity cubic-bezier(.4,0,.2,1) 436ms;
  -moz-transition:opacity cubic-bezier(.4,0,.2,1) 436ms;
  transition:opacity cubic-bezier(.4,0,.2,1) 436ms;
  z-index:9999
}
  
.insp-logo-frame{
  display:-webkit-flex;
  display:-moz-flex;
  display:flex;
  -webkit-flex-direction:column;
  -moz-flex-direction:column;
  flex-direction:column;
  -webkit-justify-content:center;
  -moz-justify-content:center;
  justify-content:center;
  -webkit-animation:fadein 436ms;
  -moz-animation:fadein 436ms;
  animation:fadein 436ms;
  height:100%
}
    
.insp-logo-frame-img{
    width:10rem;
    height:10rem;
    -webkit-align-self:center;
    -moz-align-self:center;
    align-self:center;
    border-radius:50%
  }
      
.ipl-progress-indicator-head{
background-color:white;
height:4px;
overflow:hidden;
position:relative;
}

.first-indicator,
.second-indicator{

background-color:yellow;
bottom:0;
left:0;
right:0;
top:0;
position:absolute;
-webkit-transform-origin:left center;
-moz-transform-origin:left center;
transform-origin:left center;
-webkit-transform:scaleX(0);
-moz-transform:scaleX(0);
transform:scaleX(0)
}

.first-indicator{
-webkit-animation:first-indicator 2s linear infinite;
-moz-animation:first-indicator 2s linear infinite;
animation:first-indicator 2s linear infinite
}

.second-indicator{
-webkit-animation:second-indicator 2s linear infinite;
-moz-animation:second-indicator 2s linear infinite;
animation:second-indicator 2s linear infinite
}

.insp-logo{
animation:App-logo-spin infinite 20s linear;
border-radius:50%;
-webkit-align-self:center;
-moz-align-self:center;
align-self:center;
}

@keyframes App-logo-spin{
from{transform:rotate(0)}
to{transform:rotate(360deg)}
}

@-webkit-keyframes fadein{
from{opacity:0}
to{opacity:1}}
@-moz-keyframes fadein{
from{opacity:0}
to{opacity:1}
}

@keyframes fadein{
from{opacity:0}
to{opacity:1}
}

@keyframes first-indicator{
0%{transform:translate(0) scaleX(0)}
25%{transform:translate(0) scaleX(.5)}
50%{transform:translate(25%) scaleX(.75)}
75%{transform:translate(100%) scaleX(0)}
100%{transform:translate(100%) scaleX(0)}
}

@keyframes second-indicator{
0%{transform:translate(0) scaleX(0)}
60%{transform:translate(0) scaleX(0)}
80%{transform:translate(0) scaleX(.6)}
100%{transform:translate(100%) scaleX(.1)}
}
